.App {
    text-align: center;
    font-family: "Josefin Sans", sans-serif;
  }
  
  .App-content {
    top: 0;
    left: -100%;
    position: fixed;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
  }
  
  .slide {
    left: 0;
    top: 0;
    transition: 1s all ease;
  }
  
  .night {
    display: -webkit-flex;
    display: flex;
    background: url(/static/media/night.a4671a88.jpeg) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    z-index: -1;
    background-size: cover;
    color: white;
    height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
    transition: 1s all ease-in;
  }

  .day {
    display: -webkit-flex;
    display: flex;
    background: url(/static/media/day.dad52073.jpeg) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
