  .mode {
    cursor:-webkit-grabbing;
    cursor:grabbing;
  }

  .menu {
      margin-right: 5%;
  }

  .menuIcon {
    display:none;
  }
  
  @media (max-width: 414px) {
     .menu{
      display: none;
      }
      .night {
        display: -webkit-flex;
