  .circle {
    -webkit-animation: circle-anim ease infinite;
            animation: circle-anim ease infinite;
    -webkit-transform-origin: center;
            transform-origin: center;
    transform-box: fill-box;
    -webkit-perspective: 0px;
            perspective: 0px;
  }
  
  .circle.c1 {
